import { isNil } from 'lodash';
import { Config } from '../../Config';
import { MultilangStringType } from './Types/MultilangStringType';

export const getMultilangStringValue = (country: string, string: MultilangStringType) => {
    const defaultLang = Config.countries.find(t => t.name === country)?.languages[0];

    return isNil(defaultLang)
        ? Object.values(string)[0]
        : string[defaultLang];
}