import { Strings } from "src/Localization/Strings";
import { Result } from "src/Result";

export const MinWidth = 750;
export const MinHeight = 784;
export const MaxWidth = 1500;
export const MaxHeight = 1568;

export const validateImageAsync = (blob: Blob) => {
    const promise = new Promise((resolve, reject) => {
        const img = new Image();
        img.src = window.URL.createObjectURL(blob);
        img.onload = () => {
            if (img.width < MinWidth || img.width > MaxWidth || img.height < MinHeight || img.height > MaxHeight) {
                reject();
            } else {
                resolve();
            }
        }
        img.onerror = () => reject();
    });

    return Result.FromPromise(promise).withOverridenError(Strings.imageIsNotValid);
}