import { isNil } from 'lodash';
import moment from 'moment';
import { Result } from 'src/Result';
import { Maybe } from 'src/Types/Maybe';
import { Strings } from '../../Localization/Strings';
import { KeyValueType } from '../../Types/KeyValueType';
import { WidgetType } from './Types/WidgetType';
import { validateImageAsync } from './validateImageAsync';

export const validateWidgetAsync = (widget: WidgetType, image: Maybe<Blob>): Result<void> => {
    return Result.Void()
        .onSuccess(_ => image && validateImageAsync(image))
        .onSuccess(_ => allValuesFilled(widget.button.title))
        .onSuccess(_ => allValuesFilled(widget.description))
        .onSuccess(_ => allValuesFilled(widget.title))
        .ensure(_ => moment(widget.fromDate).isBefore(widget.tillDate), Strings.dateRangeIsInvalid)
        .void;
}

const allValuesFilled = (object: KeyValueType) => Object.values(object).every(t => !isNil(t) && t.length > 0)
    ? Result.Void()
    : Result.Fail(Strings.someFieldsAreEmpty);