import { Strings } from 'src/Localization/Strings';
import { Result } from 'src/Result';
import { fetchRepositoriesAsync } from '../GitHub/fetchRepositoriesAsync';
import { setRepositories } from '../GitHub/setRepositories';
import { setUserCredentials } from './setUserCredentials';

export const loginAsync = (username: string, password: string): Result<void> => {
    setUserCredentials(username, password);

    return fetchRepositoriesAsync()
        .withOverridenError(Strings.loginFailed)
        .ensure(repositories => repositories.length > 0, Strings.noRepositoriesAvailable)
        .onSuccess(repositories => setRepositories(repositories))
}