import { Button, Dialog, DialogActions, DialogContent, DialogTitle, Grid, MenuItem, Select, TextField, Typography } from '@material-ui/core';
import merge from 'deepmerge';
import { isNil } from 'lodash';
import React, { useCallback, useEffect, useState } from "react";
import shortid from 'shortid';
import { ErrorAlert } from 'src/Components/ErrorAlert/ErrorAlert';
import { Strings } from "src/Localization/Strings";
import { validateWidgetAsync } from 'src/Services/Management/validateWidgetAsync';
import { Maybe } from 'src/Types/Maybe';
import { useConfirmResult } from '../../Hooks/useConfirmResult';
import { convertImageToBase64Async } from '../../Services/Management/convertImageToBase64Async';
import { WidgetAudiences } from '../../Services/Management/Models/WidgetAudiences';
import { WidgetType } from '../../Services/Management/Types/WidgetType';
import { DatePicker } from '../DatePicker/DatePicker';
import { Divider } from '../Divider/Divider';
import { WidgetManagerMultilangField } from '../WidgetManagerMultilangField/WidgetManagerMultilangField';
import { useStyles } from './Styles';

type WidgetManagerPropsType = {
    country: string,
    widget: WidgetType,
    open: boolean,
    onCancel: () => void,
    onDelete?: () => void,
    onSave: (widget: WidgetType, imageBase64: Maybe<string>) => void,
}

export const WidgetManager = (props: WidgetManagerPropsType) => {
    const { onSave, onDelete } = props;

    const [openCounter, setOpenCounter] = useState(0);
    const [workingCopy, setWorkingCopy] = useState<WidgetType>(props.widget);
    const [imageSource, setImageSource] = useState<Blob>();
    const [imageBase64, setImageBase64] = useState<string>();
    const [error, setError] = useState<string>();

    const styles = useStyles();

    const confirmAsync = useConfirmResult();

    useEffect(() => {
        setWorkingCopy(merge(props.widget, {}));
        setError(undefined);
    }, [props.widget, openCounter]);

    useEffect(() => setOpenCounter(t => t + 1), [props.open]);

    const onUpdate = useCallback((action: (target: WidgetType) => void) => {
        action(workingCopy);
        setWorkingCopy(merge<WidgetType>(workingCopy, {}));
    }, [workingCopy]);

    const onRemoveImage = useCallback(() => {
        onUpdate(t => t.imageUrl = "")
        setImageBase64(undefined);
        setImageSource(undefined);
    }, [onUpdate]);

    const onAddImage = useCallback((files: FileList | null) => {
        if (isNil(files) || files.length === 0) {
            return;
        }

        onUpdate(t => t.imageUrl = `${shortid.generate()}.png`);

        setImageSource(files[0]);

        convertImageToBase64Async(files[0])
            .onSuccess(base64 => {
                setImageBase64(base64);
                setError(undefined);
            })
            .onFailure(error => setError(error))
            .run();
    }, [onUpdate]);

    const onSaveInner = useCallback(() => {
        validateWidgetAsync(workingCopy, imageSource)
            .onSuccess(_ => onSave(workingCopy, imageBase64))
            .onFailure(error => setError(error))
            .run();

    }, [onSave, workingCopy, imageBase64, imageSource]);

    const onDeleteInner = useCallback(() => {
        if (isNil(onDelete)) {
            return;
        }
        
        confirmAsync(Strings.areYouSureToDeleteWidget)
            .onSuccess(_ => onDelete())
            .run();
    }, [confirmAsync, onDelete]);

    return (
        <Dialog open={props.open} fullWidth maxWidth="sm">
            <DialogTitle>
                {Strings.manageWidget}
                <ErrorAlert error={error} />
            </DialogTitle>
            <DialogContent dividers>
                <Grid container spacing={2} direction="column">
                    <WidgetManagerMultilangField
                        label={Strings.title}
                        value={workingCopy.title}
                        country={props.country}
                        onChange={(value, lang) => onUpdate(t => t.title[lang] = value)}
                    />
                    <Divider />
                    <WidgetManagerMultilangField
                        label={Strings.description}
                        value={workingCopy.description}
                        country={props.country}
                        onChange={(value, lang) => onUpdate(t => t.description[lang] = value)}
                    />
                    <Divider />
                    <Grid item>
                        <Typography variant="h6">{Strings.image}</Typography>
                    </Grid>
                    <Grid item>
                        <Typography color="textSecondary" variant="subtitle2">{Strings.pngImagesOnly}</Typography>
                    </Grid>
                    <Grid item container alignContent="center" justify="center" alignItems="center">
                        <Grid item className={styles.fileUploadContainer}>
                            <input
                                accept="image/png"
                                type="file"
                                onChange={e => onAddImage(e.target.files)}
                            />
                        </Grid>
                        {workingCopy.imageUrl.length > 0 &&
                            <Grid item>
                                <Button variant="contained" color="secondary" onClick={onRemoveImage}>
                                    {Strings.removeImage}
                                </Button>
                            </Grid>
                        }
                    </Grid>
                    <Divider />
                    <Grid item>
                        <Typography variant="h6">{Strings.moreButton}</Typography>
                    </Grid>
                    <Grid item>
                        <TextField
                            fullWidth
                            label={Strings.link}
                            type="text"
                            variant="outlined"
                            value={workingCopy.button.url}
                            error={workingCopy.button.url.length === 0}
                            onChange={e => onUpdate(t => t.button.url = e.target.value)}
                        />
                    </Grid>
                    <WidgetManagerMultilangField
                        value={workingCopy.button.title}
                        country={props.country}
                        onChange={(value, lang) => onUpdate(t => t.button.title[lang] = value)}
                    />
                    <Divider />
                    <Grid item>
                        <Typography variant="h6">{Strings.audience}</Typography>
                    </Grid>
                    <Grid item>
                        <Select
                            value={workingCopy.audience}
                            onChange={e => onUpdate(t => t.audience = e.target.value as WidgetAudiences)}
                            fullWidth
                            variant="outlined"
                        >
                            {Object.values(WidgetAudiences).map(audience => <MenuItem key={audience} value={audience}>{audience}</MenuItem>)}
                        </Select>
                    </Grid>
                    <Divider />
                    <DatePicker
                        title={Strings.fromDate}
                        value={workingCopy.fromDate}
                        onChange={date => onUpdate(t => t.fromDate = date)}
                    />
                    <Divider />
                    <DatePicker
                        title={Strings.tillDate}
                        value={workingCopy.tillDate}
                        onChange={date => onUpdate(t => t.tillDate = date)}
                    />
                </Grid>
            </DialogContent>
            <DialogActions>
                {!isNil(props.onDelete) &&
                    <Button variant="contained" color="secondary" onClick={onDeleteInner}>
                        {Strings.deleteWidget}
                    </Button>
                }
                <Button variant="contained" onClick={props.onCancel}>
                    {Strings.cancel}
                </Button>
                <Button
                    variant="contained"
                    color="primary"
                    onClick={onSaveInner}
                >
                    {Strings.save}
                </Button>
            </DialogActions>
        </Dialog>
    );
}