import { Result } from 'src/Result';
import { Maybe } from 'src/Types/Maybe';
import { sendJsonRequestAsync } from '../Http/sendJsonRequestAsync';
import { HttpRequestType } from '../Http/Types/HttpRequestType';
import { ApiRoot } from './Models/ApiRoot';
import { RepositoryOwner } from './Models/RepositoryOwner';

export const persistFileAsync = (repositoryName: string, path: string, base64Content: string, sha: Maybe<string>): Result<string> => {
    const request: HttpRequestType = {
        path: `${ApiRoot}repos/${RepositoryOwner}/${repositoryName}/contents/${path}`,
        method: "PUT",
        body: {
            message: "Publish changes",
            content: base64Content,
            sha,
        },
        useAuthorization: true,
        useJsonContentType: true,
    };
    
    return sendJsonRequestAsync<PersistResponseType>(request)
        .onSuccess(result => result.content.sha);
}

type PersistResponseType = {
    content: {
        sha: string,
    }
};