import { Grid } from '@material-ui/core';
import merge from 'deepmerge';
import { isNil } from 'lodash';
import React, { Fragment, useCallback, useEffect, useState } from 'react';
import { Divider } from 'src/Components/Divider/Divider';
import { Environment } from 'src/Components/Environment/Environment';
import { ErrorAlert } from 'src/Components/ErrorAlert/ErrorAlert';
import { Loader } from 'src/Components/Loader/Loader';
import { Config } from 'src/Config';
import { Strings } from 'src/Localization/Strings';
import { Result } from 'src/Result';
import { EnvironementDataType } from 'src/Services/Management/Types/EnvironementDataType';
import { WidgetType } from 'src/Services/Management/Types/WidgetType';
import { Maybe } from 'src/Types/Maybe';
import { parseJsonWithDates } from '../../Extensions/JsonExtensions';
import { useRouteParameter } from '../../Hooks/useRouteParameter';
import { loadCountryDataAsync } from '../../Services/Management/loadCountryDataAsync';
import { publishEnvironmentChangesAsync } from '../../Services/Management/publishEnvironmentChangesAsync';
import { CountryDataType } from '../../Services/Management/Types/CountryDataType';
import { useStyles } from './Styles';

export const ManageCountry = () => {
    const country = useRouteParameter("country");

    const [isLoading, setIsLoading] = useState(false);
    const [error, setError] = useState<string>();
    const [data, setData] = useState<CountryDataType>();
    const [lastPublishedData, setLastPublishedData] = useState<CountryDataType>();

    useEffect(() => {
        if (Config.debug) {
            const item = localStorage.getItem(Config.debugDataKey);
            if (item) {
                const newData = parseJsonWithDates(item);
                setData(newData);
                setLastPublishedData(merge(newData, {}));
                return;
            }
        } else {
            localStorage.removeItem(Config.debugCountryKey);
            localStorage.removeItem(Config.debugDataKey);
        }

        if (isNil(country)) {
            setError(Strings.somethingWentWrong);

            return;
        }

        setIsLoading(true);

        loadCountryDataAsync(country)
            .onSuccess(data => {
                if (Config.debug) {
                    data.environments.forEach(t => t.imageUrlToBase64Map = {});
                    localStorage.setItem(Config.debugCountryKey, country);
                    localStorage.setItem(Config.debugDataKey, JSON.stringify(data));
                }

                setData(data);
                setLastPublishedData(merge(data, {}));
            })
            .onFailure(error => setError(error))
            .onBoth(_ => setIsLoading(false))
            .run();
    }, [country]);

    const applyEnvironmentChange = useCallback((environment: Maybe<EnvironementDataType>, widget: Maybe<WidgetType>, imageBase64: Maybe<string>, widgetPusher: (env: EnvironementDataType) => void, hasChanges: boolean) => {
        if (isNil(data) || isNil(environment)) {
            return;
        }

        if (!isNil(imageBase64) && !isNil(widget)) {
            const newValue = environment.imageUrlToBase64Map[widget.imageUrl] || {};
            newValue.value = imageBase64;
            environment.imageUrlToBase64Map[widget.imageUrl] = newValue;
        }

        widgetPusher(environment);

        environment.hasChanges = hasChanges;

        const newData = merge(data, {});
        setData(newData);
        return newData;
    }, [data]);

    const onWidgetChanged = useCallback((environmentIndex: number, widgetIndex: number, widget: WidgetType, imageBase64: Maybe<string>) =>
        applyEnvironmentChange(data?.environments[environmentIndex], widget, imageBase64, t => t.widgets[widgetIndex] = widget, true),
        [data, applyEnvironmentChange]
    );

    const onCopyWidget = useCallback((env: string, widget: WidgetType, imageBase64: Maybe<string>) =>
        applyEnvironmentChange(data?.environments.find(t => t.name === env), widget, imageBase64, t => t.widgets.unshift(widget), true),
        [data, applyEnvironmentChange]
    );

    const onDeleteWidget = useCallback((index: number, env: EnvironementDataType) =>
        applyEnvironmentChange(env, undefined, undefined, t => t.widgets.splice(index, 1), true),
        [applyEnvironmentChange]
    );

    const onCreateWidget = useCallback((env: EnvironementDataType, widget: WidgetType, imageBase64: Maybe<string>) =>
        applyEnvironmentChange(env, widget, imageBase64, t => t.widgets.unshift(widget), true),
        [applyEnvironmentChange]
    );

    const onWidgetReordered = useCallback((env: EnvironementDataType, from: number, to: number) =>
        applyEnvironmentChange(env, undefined, undefined, t => t.widgets.move(from, to), true),
        [applyEnvironmentChange]
    );

    const onRevertChanges = useCallback((env: EnvironementDataType) =>
        applyEnvironmentChange(env, undefined, undefined, t => {
            const lastPublishedEnvironment = lastPublishedData?.environments.find(t => t.name === env.name);

            if (isNil(lastPublishedEnvironment)) { return; }

            t.widgets = merge(lastPublishedEnvironment.widgets, []);
            t.imageUrlToBase64Map = merge(lastPublishedEnvironment.imageUrlToBase64Map, {});
        }, false),
        [applyEnvironmentChange, lastPublishedData]
    );

    const onPublishChanges = useCallback((env: EnvironementDataType): Result<void> => {
        const publishedEnv = lastPublishedData?.environments.find(t => t.name === env.name);

        if (isNil(country) || isNil(publishedEnv) || isNil(data)) {
            return Result.Fail(Strings.somethingWentWrong);
        }

        return publishEnvironmentChangesAsync(country, env, publishedEnv)
            .onSuccess(result => applyEnvironmentChange(env, undefined, undefined, t => {
                t.sha = result.environmentSha;

                Object.keys(t.imageUrlToBase64Map).forEach(key => {
                    const newSha = result.imagesUrlToShaMap.get(key);
                    if (!isNil(newSha)) {
                        t.imageUrlToBase64Map[key].sha = newSha;
                    }
                });
            }, false))
            .onSuccess(newData => {
                if (isNil(newData)) { return; }

                setLastPublishedData(merge(newData, {}));
            })
    }, [lastPublishedData, country, data, applyEnvironmentChange]);

    const styles = useStyles();

    return (
        <Grid container direction="column" className={styles.container}>
            <ErrorAlert error={error} />
            <Loader visible={isLoading} />
            {!isNil(country) && !isNil(data) &&
                data.environments.map((environment, environmentIndex) => (
                    <Fragment key={environment.name}>
                        <Environment
                            country={country}
                            {...environment}
                            onWidgetChanged={(widgetIndex, widget, image64) => onWidgetChanged(environmentIndex, widgetIndex, widget, image64)}
                            onCopyWidget={onCopyWidget}
                            onDeleteWidget={index => onDeleteWidget(index, environment)}
                            onCreateWidget={(widget, image64) => onCreateWidget(environment, widget, image64)}
                            onWidgetReordered={(from, to) => onWidgetReordered(environment, from, to)}
                            onRevertChanges={() => onRevertChanges(environment)}
                            onPublishChangesAsync={() => onPublishChanges(environment)}
                        />
                        <Divider />
                    </Fragment>
                ))
            }
        </Grid>
    )
}