import { Strings } from 'src/Localization/Strings';
import { Result } from 'src/Result';
import { parseJsonWithDates } from '../../Extensions/JsonExtensions';
import { fetchFilesAsync } from '../GitHub/fetchFilesAsync';
import { getRepositoryName } from '../GitHub/getRepositoryName';
import { RepositoryContentType } from '../GitHub/Types/RepositoryContentType';
import { sendRequestAsync } from '../Http/sendRequestAsync';
import { arrayBufferToBase64 } from './arrayBufferToBase64';
import { getEnvironments } from './getEnvironments';
import { CountryDataType } from './Types/CountryDataType';
import { EnvironementDataType } from './Types/EnvironementDataType';
import { ImageUrlToBase64MapType } from './Types/ImageUrlToBase64MapType';
import { ImageUrlValueType } from './Types/ImageUrlValueType';
import { WidgetType } from './Types/WidgetType';
import { getEnvironmentJsonFileName } from './getEnvironmentJsonFileName';

export const loadCountryDataAsync = (country: string): Result<CountryDataType> => {
    const repository = getRepositoryName(country);
    const environments = getEnvironments(country);
    const environmentFileNames = environments.map(t => getEnvironmentJsonFileName(t));

    const environmentToDataMap = new Map<string, EnvironementDataType>();

    return fetchFilesAsync(repository, "")
        .onSuccess(data => data.filter(t => environmentFileNames.some(x => x === t.name)))
        .onSuccess(environemntFiles => Result
            .Combine(environemntFiles.map(content => downloadEnvironmentAsync(repository, content).onSuccess(data => environmentToDataMap.set(data.name, data))))
            .onSuccess(_ => environments.compactMap(environment => environmentToDataMap.get(environment)))
        )
        .onSuccess((environments): CountryDataType => ({
            country,
            environments,
        }))
        .withOverridenError(Strings.somethingWentWrong);
}

export const getEnvironmentFromJsonFileName = (fileName: string) => fileName.split(".")[0];

const downloadEnvironmentAsync = (repository: string, content: RepositoryContentType): Result<EnvironementDataType> => {
    const environment = getEnvironmentFromJsonFileName(content.name);

    return downloadRawTextFileAsync(content.download_url)
        .onSuccess(json => parseJsonWithDates(json) as WidgetType[])
        .onFailureCompensate(_ => [])
        .onSuccess(widgets => downloadEnvironmentImagesAsync(repository, environment, widgets)
            .onSuccess((imageUrlToBase64Map): EnvironementDataType => ({
                name: environment,
                sha: content.sha,
                widgets,
                hasChanges: false,
                imageUrlToBase64Map: convertMapToKeyValueObj(imageUrlToBase64Map),
            }))
        );
}

const convertMapToKeyValueObj = (map: Map<string, ImageUrlValueType>): ImageUrlToBase64MapType =>
    Array.from(map).reduce((obj, [key, image]) => Object.assign(obj, { [key]: { sha: image.sha, value: image.value } }), {});

const downloadEnvironmentImagesAsync = (repository: string, environment: string, widgets: WidgetType[]): Result<Map<string, ImageUrlValueType>> => {
    const imagesInUseSet = widgets.filter(t => t.imageUrl).map(t => t.imageUrl).reduce((result, current) => result.add(current) && result, new Set<string>());

    return fetchFilesAsync(repository, `images/${environment}`)
        .onFailureCompensate(_ => [])
        .onSuccess(imagesContent => imagesContent.filter(t => imagesInUseSet.has(t.name)))
        .onSuccess(imagesContent => Result
            .Combine(imagesContent.map(t => downloadRawBase64FileAsync(t.download_url).onSuccess(file => ({ name: t.name, value: file, sha: t.sha }))))
            .onSuccess(downloadedImages => {
                const map = new Map<string, ImageUrlValueType>();

                downloadedImages.forEach((image, index) => map.set(image.name, {
                    value: image.value,
                    sha: image.sha,
                }));

                return map;
            })
        );
}

const downloadRawTextFileAsync = (url: string): Result<string> =>
    sendRequestAsync({ path: url, method: "GET", ignoreCacheControl: true, })
        .onSuccess(response => response.text());

const downloadRawBase64FileAsync = (url: string): Result<string> =>
    sendRequestAsync({ path: url, method: "GET", ignoreCacheControl: true, })
        .onSuccess(response => response.arrayBuffer())
        .onSuccess(buffer => arrayBufferToBase64(buffer));