import { getLanguages } from './getLanguages';
import { WidgetAudiences } from './Models/WidgetAudiences';
import { MultilangStringType } from './Types/MultilangStringType';
import { WidgetType } from './Types/WidgetType';

export const createWidget = (country: string): WidgetType => {
    const languages = getLanguages(country);

    return {
        title: createMultilangString(languages),
        description: createMultilangString(languages),
        fromDate: new Date(),
        tillDate: new Date(),
        audience: WidgetAudiences.all,
        imageUrl: "",
        button: {
            url: "",
            title: createMultilangString(languages),
        }
    }
}

const createMultilangString = (langs: string[]): MultilangStringType => {
    return langs.reduce((obj, lang) => Object.assign(obj, { [lang]: "" }), {});
}