import { isNil } from 'lodash';
import React, { useEffect } from "react";
import { useHistory } from "react-router-dom";
import { Loader } from "src/Components/Loader/Loader";
import { Config } from "src/Config";

type StartPropsType = {
    onCompleted: () => void;
}

export const Start = (props: StartPropsType) => {
    const { onCompleted } = props;

    const history = useHistory();

    useEffect(() => {
        if (Config.debug && !isNil(localStorage.getItem(Config.debugCountryKey))) {
            onCompleted();
            history.push(`/manage/country/${localStorage.getItem(Config.debugCountryKey)}`);
            return;
        }

        history.push("/login");

        onCompleted();
    }, [onCompleted, history]);

    return (
        <Loader visible />
    )
}