import moment from 'moment';
import { Result } from 'src/Result';
import { sendRequestAsync } from '../Http/sendRequestAsync';
import { HttpRequestType } from '../Http/Types/HttpRequestType';
import { ApiRoot } from './Models/ApiRoot';
import { RepositoryOwner } from './Models/RepositoryOwner';

export const createReleaseAsync = (repositoryName: string, environment: string): Result<void> => {
    const request: HttpRequestType = {
        path: `${ApiRoot}repos/${RepositoryOwner}/${repositoryName}/releases`,
        method: "POST",
        body: {
            tag_name: `${environment}_${moment.utc().unix()}`,
        },
        useAuthorization: true,
        useJsonContentType: true,
    };
    
    return sendRequestAsync(request).void;
}