import moment from 'moment';
import { JsonDateTimeFormat } from './DateExtensions';
//@ts-ignore
const parseLegacy = JSON.parse;

const parseDate = (key: string, value: any) => {
    if (typeof value !== 'string') {
        return value;
    }

    const formattedDate = moment.utc(value.replace("T", " "), JsonDateTimeFormat).format(JsonDateTimeFormat);

    if (formattedDate === value) {
        return moment.utc(value, JsonDateTimeFormat).toDate();
    }

    return value;
};

export const parseJsonWithDates = (text: string, reviver?: (key: string, value: any) => any) => {
    return parseLegacy(text, (key, value) => (reviver && reviver(key, value)) || parseDate(key, value))
}

JSON.parse = parseJsonWithDates;