import { useMemo } from "react";
import { getMultilangStringValue } from "src/Services/Management/getMultilangStringValue";
import { MultilangStringType } from '../Services/Management/Types/MultilangStringType';

export const useMultilangString = (country: string, string: MultilangStringType) => useMemo(() => getMultilangStringValue(country, string), [country, string]);