import { sendJsonRequestAsync } from '../Http/sendJsonRequestAsync';
import { HttpRequestType } from '../Http/Types/HttpRequestType';
import { isValidRepository } from './isValidRepository';
import { ApiRoot } from './Models/ApiRoot';
import { RepositoryOwner } from './Models/RepositoryOwner';
import { RepositoryType } from './Types/RepositoryType';

export const fetchRepositoriesAsync = () => {
    const request: HttpRequestType = {
        path: `${ApiRoot}user/repos?per_page=1000`,
        method: "GET",
        useAuthorization: true,
    };
    
    return sendJsonRequestAsync<RepositoryType[]>(request)
        .onSuccess(repositories => repositories.filter(t => isValidRepository(t.name) && t.owner.login === RepositoryOwner));
}