import { Button, Dialog, DialogActions, DialogContent, DialogTitle, FormControlLabel, Radio, RadioGroup } from '@material-ui/core';
import { isNil } from 'lodash';
import React, { useCallback, useEffect, useState } from "react";
import { useHistory } from 'react-router-dom';
import { Strings } from "src/Localization/Strings";
import { getRepositories } from '../../Services/GitHub/getRepositories';
import { RepositoryType } from '../../Services/GitHub/Types/RepositoryType';
import { extractCountryName } from '../../Services/GitHub/extractCountryName';

export const PickRepository = () => {
    const history = useHistory();

    const [repositories, setRepositories] = useState<RepositoryType[]>([]);
    const [selectedRepository, setSelectedRepository] = useState<RepositoryType>();

    useEffect(() => setRepositories(getRepositories() || []), []);

    const onSelectRepository = useCallback((name: string) => 
        setSelectedRepository(repositories.find(t => t.name === name)), 
        [repositories]
    );

    const onStartEditing = useCallback(() => {
        if (isNil(selectedRepository)) {
            return;
        }
        
        const maybeCountry = extractCountryName(selectedRepository.name);

        if (!isNil(maybeCountry)) {
            history.replace(`/manage/country/${maybeCountry}`);
        }
    }, [selectedRepository, history]);

    return (
        <Dialog open>
            <DialogTitle>{Strings.pickRepository}</DialogTitle>
            <DialogContent dividers>
                <RadioGroup
                    onChange={e => onSelectRepository(e.target.value)}
                >
                    {repositories.map(option => (
                        <FormControlLabel
                            value={option.name}
                            key={option.name}
                            control={<Radio />}
                            label={option.name}
                        />
                    ))}
                </RadioGroup>
            </DialogContent>
            <DialogActions>
                <Button variant="contained" color="primary" onClick={onStartEditing}>
                    {Strings.startEditing}
                </Button>
            </DialogActions>
        </Dialog>
    );
}