import React, { Fragment, useState } from "react";
import { BrowserRouter as Router, Route } from "react-router-dom";
import { Login } from "src/Pages/Login/Login";
import { PickRepository } from "src/Pages/PickRepository/PickRepository";
import { Start } from "src/Pages/Start/Start";
import { ManageCountry } from "src/Pages/ManageCountry/ManageCountry";

export const Navigation = () => {
    const [isStarted, setIsStarted] = useState<boolean>(false);

    return (
        <Router>
            {!isStarted &&
                <Start onCompleted={() => setIsStarted(true)} />
            }
            {isStarted &&
                <Fragment>
                     <Route path={`/login`} component={Login} exact />
                     <Route path={`/pick`} component={PickRepository} exact />
                     <Route path={`/manage/country/:country`} component={ManageCountry} exact />
                </Fragment>
            }
        </Router>
    )
}