import { Box, Button, Grid, Link, Typography } from '@material-ui/core';
import moment from 'moment';
import React, { useCallback, useMemo, useState, Fragment } from 'react';
import useHover from 'react-use-hover';
import { Strings } from 'src/Localization/Strings';
import { WidgetType } from 'src/Services/Management/Types/WidgetType';
import { Maybe } from 'src/Types/Maybe';
import { useMultilangString } from '../../Hooks/useMultilangString';
import { WidgetCopier } from '../WidgetCopier/WidgetCopier';
import { WidgetManager } from '../WidgetManager/WidgetManager';
import { useStyles } from './Styles';

type WidgetPropsType = {
    country: string,
    widget: WidgetType,
    imageBase64: Maybe<string>,
    onChanged: (widget: WidgetType, imageBase64: Maybe<string>) => void,
    onCopy: (env: string, widget: WidgetType, imageBase64: Maybe<string>) => void,
    onDelete: () => void,
};

export const Widget = (props: WidgetPropsType) => {
    const { onChanged, onCopy, onDelete } = props;

    const styles = useStyles();

    const title = useMultilangString(props.country, props.widget.title);
    const description = useMultilangString(props.country, props.widget.description);
    const buttonTitle = useMultilangString(props.country, props.widget.button.title);
    const backgroundImage = useMemo(() => props.imageBase64 && `url("data:image/png;base64,${props.imageBase64}")`, [props.imageBase64]);

    const availabilityString = useMemo(() =>
        `${moment.utc(props.widget.fromDate).format("ll")} - ${moment.utc(props.widget.tillDate).format("ll")}`,
        [props.widget.fromDate, props.widget.tillDate]
    );

    const isActive = useMemo(() => {
        const current = moment.utc();

        return current.isSameOrAfter(props.widget.fromDate) && current.isSameOrBefore(props.widget.tillDate);
    }, [props.widget.fromDate, props.widget.tillDate]);

    const containerClass = useMemo(() => `${styles.container} ${isActive ? "" : styles.containerInactive}`, [isActive, styles.container, styles.containerInactive]);

    const [isManagementOpen, setIsManagementOpen] = useState(false);
    const [isCopyOpen, setIsCopyOpen] = useState(false);

    const onSave = useCallback((widget: WidgetType, imageBase64: Maybe<string>) => {
        onChanged(widget, imageBase64);
        setIsManagementOpen(false);
    }, [onChanged]);

    const onCopyInner = useCallback((env: string) => {
        onCopy(env, props.widget, props.imageBase64);
        setIsCopyOpen(false);
    }, [onCopy, props.widget, props.imageBase64]);

    const onDeleteInner = useCallback(() => {
        onDelete();
        setIsManagementOpen(false);
    }, [onDelete]);

    const [isHover, hoverProps] = useHover();

    return (
        <Box className={styles.wrapper} {...hoverProps}>
            <Box className={containerClass} style={{ backgroundImage }}>
                <Grid container direction="column" alignItems="center" justify="space-between" className={styles.contentContainer}>
                    <Grid item>
                        <Typography className={styles.title} variant="h5" gutterBottom>{title}</Typography>
                    </Grid>
                    <Grid item>
                        <Typography className={styles.description} variant="body1" gutterBottom>{description}</Typography>
                    </Grid>
                    <Grid item>
                        <Typography>
                            <Link
                                className={styles.button}
                                target="_blank"
                                href={props.widget.button.url}
                            >
                                {buttonTitle}
                            </Link>
                        </Typography>
                    </Grid>
                </Grid>
            </Box>
            <Grid container className={styles.actionsContainer} spacing={1} alignContent="center" alignItems="center">
                <Grid item>
                    <Typography variant="subtitle2" color="textSecondary">{props.widget.audience}</Typography>
                    <Typography variant="subtitle2" color="textSecondary">{availabilityString}</Typography>
                </Grid>
                {isHover &&
                    <Fragment>
                        <Grid item>
                            <Button
                                variant="contained"
                                onClick={() => setIsManagementOpen(true)}
                            >
                                {Strings.edit}
                            </Button>
                        </Grid>
                        <Grid item>
                            <Button
                                variant="contained"
                                onClick={() => setIsCopyOpen(true)}
                            >
                                {Strings.copy}
                            </Button>
                        </Grid>
                    </Fragment>
                }
            </Grid>
            <WidgetManager
                country={props.country}
                widget={props.widget}
                open={isManagementOpen}
                onCancel={() => setIsManagementOpen(false)}
                onSave={onSave}
                onDelete={onDeleteInner}
            />
            <WidgetCopier
                country={props.country}
                open={isCopyOpen}
                onCancel={() => setIsCopyOpen(false)}
                onCopy={environment => onCopyInner(environment)}
            />
        </Box>
    );
}