import { isNil } from 'lodash';
import { Result } from 'src/Result';
import { createReleaseAsync } from '../GitHub/createReleaseAsync';
import { getRepositoryName } from '../GitHub/getRepositoryName';
import { persistFileAsync } from '../GitHub/persistFileAsync';
import { getEnvironmentJsonFileName } from './getEnvironmentJsonFileName';
import { EnvironementDataType } from './Types/EnvironementDataType';

export const publishEnvironmentChangesAsync = (country: string, environment: EnvironementDataType, publishedEnvironment: EnvironementDataType): Result<PublishResultType> => {
    const repository = getRepositoryName(country);
    const newImages = environment.widgets.map(t => t.imageUrl).filter(t => !isNil(environment.imageUrlToBase64Map[t]) && isNil(publishedEnvironment.imageUrlToBase64Map[t]));

    const imagesUrlToShaMap = new Map<string, string>();

    return Result
        .Combine(newImages
            .map(t => persistFileAsync(repository, `images/${environment.name}/${t}`, environment.imageUrlToBase64Map[t].value, environment.imageUrlToBase64Map[t].sha)
                .onSuccess(sha => imagesUrlToShaMap.set(t, sha))
            )
        )
        .onSuccess(_ => persistFileAsync(repository, getEnvironmentJsonFileName(environment.name), btoa(JSON.stringify(environment.widgets, undefined, 4)), environment.sha))
        .onSuccess(environmentSha => createReleaseAsync(repository, environment.name).withOverridenValue(environmentSha))
        .onSuccess((environmentSha): PublishResultType => ({
            environmentSha,
            imagesUrlToShaMap
        }));
}

type PublishResultType = {
    environmentSha: string,
    imagesUrlToShaMap: Map<string, string>,
}