import { isNil } from "lodash";
import { useRouteMatch } from 'react-router';
import { useMemo } from 'react';

type MatchType = {
    [key: string]: string,
}

export const useRouteParameter = (name: string) => {
    const match = useRouteMatch<MatchType>();
    const result = useMemo(() => {
        return isNil(match) || isNil(match.params)
            ? undefined 
            : match.params[name]
    }, [match, name]);

    return result;
}