import { Button, Grid, Typography } from '@material-ui/core';
import { isNil } from 'lodash';
import { useSnackbar } from 'material-ui-snackbar-provider';
import React, { Fragment, useCallback, useState } from 'react';
import { DragDropContext, Draggable, Droppable, DropResult } from 'react-beautiful-dnd';
import { Strings } from 'src/Localization/Strings';
import { Result } from 'src/Result';
import { WidgetType } from 'src/Services/Management/Types/WidgetType';
import { Maybe } from 'src/Types/Maybe';
import { v4 } from 'uuid';
import { useConfirmResult } from '../../Hooks/useConfirmResult';
import { createWidget } from '../../Services/Management/createWidget';
import { ImageUrlToBase64MapType } from '../../Services/Management/Types/ImageUrlToBase64MapType';
import { Loader } from '../Loader/Loader';
import { Widget } from '../Widget/Widget';
import { WidgetManager } from '../WidgetManager/WidgetManager';
import { useStyles } from './Styles';

type EnvironmentPropsType = {
    country: string,
    hasChanges: boolean,
    name: string,
    widgets: WidgetType[],
    imageUrlToBase64Map: ImageUrlToBase64MapType,
    onWidgetChanged: (index: number, widget: WidgetType, imageBase64: Maybe<string>) => void,
    onCopyWidget: (env: string, widget: WidgetType, imageBase64: Maybe<string>) => void,
    onDeleteWidget: (index: number) => void,
    onCreateWidget: (widget: WidgetType, imageBase64: Maybe<string>) => void,
    onWidgetReordered: (fromIndex: number, toIndex: number) => void,
    onRevertChanges: () => void,
    onPublishChangesAsync: () => Result<void>,
}

export const Environment = (props: EnvironmentPropsType) => {
    const confirmAsync = useConfirmResult();
    const snackbar = useSnackbar()

    const { onCreateWidget, onRevertChanges, onCopyWidget, onDeleteWidget, onWidgetChanged, onWidgetReordered, onPublishChangesAsync } = props;

    const styles = useStyles();

    const [isLoading, setIsLoading] = useState(false);
    const [isWidgetCreationOpen, setIsWidgetCreationOpen] = useState(false);
    const [createdWidget, setCreatedWidget] = useState<WidgetType>(createWidget(props.country));

    const [widgetToIdMap] = useState(new Map<WidgetType, string>());

    const onTriggerCreateWidget = useCallback(() => {
        setCreatedWidget(createWidget(props.country));
        setIsWidgetCreationOpen(true);
    }, [props.country]);

    const onCreateWidgetInner = useCallback((widget: WidgetType, imageBase64: Maybe<string>) => {
        onCreateWidget(widget, imageBase64)
        setIsWidgetCreationOpen(false);
    }, [onCreateWidget]);

    const getWidgetId = (widget: WidgetType) => widgetToIdMap.get(widget) || widgetToIdMap.set(widget, v4()).get(widget) || "";

    const onWidgetReorderedInner = useCallback((result: DropResult) => {
        if (isNil(result.destination)) { return; }
        onWidgetReordered(result.source.index, result.destination.index);
    }, [onWidgetReordered]);

    const onRevertChangesInner = useCallback(() => {
        confirmAsync(`${Strings.areYouSureToRevert} ${props.name}?`)
            .onSuccess(_ => onRevertChanges())
            .run();
    }, [props.name, confirmAsync, onRevertChanges]);

    const onPublishInner = useCallback(() => {
        confirmAsync(`${Strings.areYouSureToPublish} ${props.name}?`)
            .onSuccess(_ => {
                setIsLoading(true);

                return onPublishChangesAsync()
                    .onFailure(_ => snackbar.showMessage(Strings.somethingWentWrong));
            })
            .onSuccess(_ => {
                snackbar.showMessage(Strings.successfullyPublished);
            })
            .onBoth(_ => setIsLoading(false))
            .run();
    }, [onPublishChangesAsync, props.name, confirmAsync, snackbar]);

    return (
        <Grid container direction="column">
            <Grid container item spacing={3} alignContent="center" className={styles.headerContainer}>
                <Grid item>
                    <Typography variant="h5" gutterBottom>{props.name}</Typography>
                </Grid>
                <Grid item>
                    <Button
                        variant="contained"
                        onClick={onTriggerCreateWidget}
                    >
                        {Strings.createWidget}
                    </Button>
                </Grid>
                {props.hasChanges &&
                    <Fragment>
                        <Grid item>
                            <Button
                                variant="contained"
                                color="primary"
                                onClick={onPublishInner}
                            >
                                {Strings.publishChanges}
                            </Button>
                        </Grid>
                        <Grid item>
                            <Button
                                variant="contained"
                                color="secondary"
                                onClick={onRevertChangesInner}
                            >
                                {Strings.revertChanges}
                            </Button>
                        </Grid>
                    </Fragment>
                }
            </Grid>
            <Grid item>
                <DragDropContext onDragEnd={onWidgetReorderedInner}>
                    <Droppable droppableId="droppable" direction="horizontal">
                        {provided => (
                            <div ref={provided.innerRef} className={styles.widgetsContainer} {...provided.droppableProps}>
                                {props.widgets.map((widget, index) =>
                                    <Draggable key={getWidgetId(widget)} draggableId={getWidgetId(widget)} index={index}>
                                        {provided => (
                                            <div
                                                ref={provided.innerRef}
                                                {...provided.draggableProps}
                                                {...provided.dragHandleProps}
                                            >
                                                <Widget
                                                    key={`${widget.title}-${index}`}
                                                    country={props.country}
                                                    imageBase64={props.imageUrlToBase64Map[widget.imageUrl]?.value || ""}
                                                    widget={widget}
                                                    onChanged={(widget, image64) => onWidgetChanged(index, widget, image64)}
                                                    onCopy={(env, widget, image64) => onCopyWidget(env, widget, image64)}
                                                    onDelete={() => onDeleteWidget(index)}
                                                />
                                            </div>
                                        )}
                                    </Draggable>
                                )}
                                {provided.placeholder}
                            </div>
                        )}
                    </Droppable>
                </DragDropContext>
            </Grid>
            <WidgetManager
                country={props.country}
                widget={createdWidget}
                open={isWidgetCreationOpen}
                onCancel={() => setIsWidgetCreationOpen(false)}
                onSave={(widget, image64) => onCreateWidgetInner(widget, image64)}
            />
            <Loader visible={isLoading} />
        </Grid >
    );
}