import { Button, Dialog, DialogActions, DialogContent, DialogTitle, MenuItem, Select } from '@material-ui/core';
import React, { useCallback, useMemo, useState } from "react";
import { Strings } from "src/Localization/Strings";
import { getEnvironments } from '../../Services/Management/getEnvironments';

type WidgetCopierPropsType = {
    country: string,
    open: boolean,
    onCancel: () => void,
    onCopy: (environment: string) => void,
}

export const WidgetCopier = (props: WidgetCopierPropsType) => {
    const { onCopy } = props;

    const environments = useMemo(() => getEnvironments(props.country), [props.country]);

    const [selectedEnvironment, setSelectedEnvironment] = useState<string>(environments[0]);

    const onCopyInner = useCallback(() => selectedEnvironment && onCopy(selectedEnvironment), [onCopy, selectedEnvironment]);

    return (
        <Dialog open={props.open} fullWidth maxWidth="sm">
            <DialogTitle>{Strings.copyWidget}</DialogTitle>
            <DialogContent dividers>
                <Select
                    onChange={e => setSelectedEnvironment(e.target.value as string || environments[0])}
                    fullWidth
                    variant="outlined"
                >
                    {environments.map(environemnt => <MenuItem key={environemnt} value={environemnt}>{environemnt}</MenuItem>)}
                </Select>
            </DialogContent>
            <DialogActions>
                <Button variant="contained" onClick={props.onCancel}>
                    {Strings.cancel}
                </Button>
                <Button variant="contained" color="primary" onClick={onCopyInner}>
                    {Strings.copy}
                </Button>
            </DialogActions>
        </Dialog>
    );
}