import { Maybe } from 'src/Types/Maybe';
import { Config } from '../../Config';
import { RepositoryNamePrefix } from './Models/RepositoryNamePrefix';

export const extractCountryName = (repositoryName: string): Maybe<string> => {
    const loweredRepositoryName = repositoryName.toLowerCase();
    const availableNames = Config.countries.map(t => `${RepositoryNamePrefix}${t.name}`.toLowerCase());

    return availableNames.find(t => t === loweredRepositoryName)?.substr(RepositoryNamePrefix.length).toUpperCase();
}