/* eslint-disable no-extend-native */
import { isNil } from 'lodash';

declare global {
    interface Array<T> {
        compactMap<K>(predicate: (arg: T) => K | undefined, checkForNilOnly?: boolean): K[];
        move(from: number, to: number): T[];
    }
}

Array.prototype.compactMap = function <T, K>(predicate: (arg: T) => K | undefined, checkForNilOnly?: boolean): K[] {
    const result = new Array<K>();

    this.forEach(item => {
        const newItem = predicate(item);

        if (checkForNilOnly === true) {
            if (!isNil(newItem)) {
                result.push(newItem);
            }
        } else {
            if (newItem) {
                result.push(newItem);
            }
        }
    });

    return result;
}

Array.prototype.move = function <T>(from: number, to: number): T[] {
    this.splice(to, 0, this.splice(from, 1)[0]);

    return this;
}