import { Result } from "src/Result";
import { getUserCredentials } from "../Authorization/getUserCredentials";
import { HttpRequestKeyValuesType } from "./Types/HttpRequestKeyValuesType";
import { HttpRequestType } from './Types/HttpRequestType';

export const sendRequestAsync = (request: HttpRequestType): Result<Response> => {
    const headers = getHeaders(request);

    const params: RequestInit = {
        method: request.method,
        headers,
        mode: "cors",
    };

    if (request.body) {
        params.body = request.useJsonContentType
            ? JSON.stringify(request.body)
            : request.body as any;
    }

    const url = getUrl(request.path, request.queryParams);

    return Result.FromPromise(fetch(url, params))
        .onSuccess(response => response.status >= 200 && response.status < 300
            ? Result.Ok(response)    
            : Result.Fail(`${response.status}`)
        );
}

const getHeaders = (request: HttpRequestType): HttpRequestKeyValuesType => {
    let headers: HttpRequestKeyValuesType = {
        ...request.headers
    };

    if (request.ignoreCacheControl !== true) {
        headers = { "If-Modified-Since": new Date().toLocaleString() };
    }

    if (request.useJsonContentType) {
        headers = { "Content-Type": "application/json", ...headers };
    }

    if (request.useAuthorization) {
        headers = { "Authorization": `Basic ${getUserCredentials()}`, ...headers };
    }

    return headers;
}

const getUrl = (path: string, queryParams?: HttpRequestKeyValuesType): string => {
    return `${path}${createQueryString(queryParams)}`;
}

const createQueryString = (queryParams?: HttpRequestKeyValuesType): string => {
    if (!queryParams) {
        return "";
    }

    const keyValues = Object
        .keys(queryParams)
        .filter(key => queryParams[key] === 'boolean' || !!queryParams[key])
        .map(key => `${key}=${queryParams[key]}`)
        .join('&');

    return `?${keyValues}`;
}