import { Grid, TextField, Typography } from '@material-ui/core';
import { isNil } from 'lodash';
import React, { useMemo } from "react";
import { getLanguages } from '../../Services/Management/getLanguages';
import { MultilangStringType } from '../../Services/Management/Types/MultilangStringType';

type WidgetManagerMultilangFieldPropsType = {
    country: string,
    label?: string,
    value: MultilangStringType,
    onChange: (value: string, language: string) => void,
}

export const WidgetManagerMultilangField = (props: WidgetManagerMultilangFieldPropsType) => {
    const languages = useMemo(() => getLanguages(props.country), [props.country]);

    return (
        <Grid item container direction="column" spacing={2}>
            {!isNil(props.label) &&
                <Grid item>
                    <Typography variant="h6">{props.label}</Typography>
                </Grid>
            }
            {languages.map(language =>
                <Grid item key={language}>
                    <TextField
                        fullWidth
                        label={language}
                        type="text"
                        variant="outlined"
                        error={props.value[language]?.length === 0}
                        value={props.value[language]}
                        onChange={e => props.onChange(e.target.value, language)}
                    />
                </Grid>
            )}
        </Grid>
    )
}