import { Button, Dialog, DialogActions, DialogContent, DialogTitle, Grid, Link, TextField, Typography } from '@material-ui/core';
import React, { Fragment, useCallback, useState } from "react";
import { useHistory } from 'react-router-dom';
import { ErrorAlert } from 'src/Components/ErrorAlert/ErrorAlert';
import { Loader } from "src/Components/Loader/Loader";
import { Strings } from "src/Localization/Strings";
import { loginAsync } from '../../Services/Authorization/loginAsync';

export const Login = () => {
    const history = useHistory();

    const [isLoading, setIsLoading] = useState(false);
    const [error, setError] = useState<string>();
    const [username, setUsername] = useState<string>("");
    const [password, setPassword] = useState<string>("");

    const onLogin = useCallback(() => {
        setIsLoading(true);

        loginAsync(username, password)
            .onFailure(error => setError(error))
            .onSuccess(_ => history.replace("/pick"))
            .onBoth(_ => setIsLoading(false))
            .run();
    }, [password, username, history]);

    return (
        <Fragment>
            <Dialog open>
                <DialogTitle>{Strings.loginTitle}</DialogTitle>
                <DialogContent dividers>
                    <Grid container spacing={2} direction="column">
                        <ErrorAlert error={error} />
                        <Grid item>
                            <TextField
                                autoFocus
                                fullWidth
                                required
                                label={Strings.userName}
                                type="text"
                                onChange={e => setUsername(e.target.value)}
                            />
                        </Grid>
                        <Grid item>
                            <TextField
                                fullWidth
                                required
                                label={Strings.pat}
                                type="password"
                                onChange={e => setPassword(e.target.value)}
                            />
                        </Grid>
                    </Grid>
                </DialogContent>
                <DialogActions>
                    <Typography>
                        <Link target="_blank" href="https://github.com/instant-widgets-editor/instant-widgets-editor.github.io/blob/master/README.md#instant-widgets-editor">
                            {Strings.instructions}
                        </Link>
                    </Typography>
                    <Button variant="contained" color="primary" onClick={onLogin}>
                        {Strings.login}
                    </Button>
                </DialogActions>
            </Dialog>
            <Loader visible={isLoading} />
        </Fragment>
    )
}